.dashboard {
  display: flex;
  min-height: calc(100vh - 70px);
  height: auto;
}

.main {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
}

.header {
  margin-bottom: 2.5rem;
}

.header h1 {
  font-size: 2.25rem;
  font-weight: 700;
  color: #1e293b;
  margin: 0 0 0.5rem 0;
}

.header p {
  color: #64748b;
  margin: 0;
}

.dashboardGrid {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 2rem;
  margin: 0 auto;
}

.kpisSection h3 {
  font-size: 0.875rem;
  font-weight: 700;
  color: #1e293b;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin: 0 0 1rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.statusIndicators {
  display: flex;
  gap: 0.5rem;
}

.statusOk, .statusDelay, .statusSupport {
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  font-size: 0.625rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.statusOk {
  background: #dcfce7;
  color: #166534;
}

.statusDelay {
  background: #fef2f2;
  color: #dc2626;
}

.statusSupport {
  background: #ffedd5;
  color: #ea580c;
}

.activitySection h3 {
  font-size: 0.875rem;
  font-weight: 700;
  color: #1e293b;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin: 0 0 1rem 0;
}
.statusHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2rem 0 1rem 0;
}

.statusHeader h3 {
  font-size: 0.875rem;
  font-weight: 700;
  color: #1e293b;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin: 0;
}
