.metricsPage {
  flex: 1;
  display: flex;
  flex-direction: column;
  background: white;
  margin: 1rem;
  border-radius: 0.75rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  overflow: hidden;
}

.metricsHeader {
  background: white;
  padding: 1rem 1.5rem;
  border-bottom: 1px solid #e2e8f0;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.backButton {
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  color: #64748b;
  font-size: 0.875rem;
  cursor: pointer;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.backButton:hover {
  background: #f1f5f9;
  color: #374151;
  border-color: #cbd5e1;
}

.headerContent {
  flex: 1;
}

.metricsTitle {
  font-size: 1.25rem;
  font-weight: bold;
  color: #1e293b;
  margin: 0 0 0.25rem 0;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.metricsTitle i {
  color: #3b82f6;
}

.metricsSubtitle {
  color: #64748b;
  font-size: 0.75rem;
  margin: 0;
}

.metricsContent {
  flex: 1;
  padding: 1.5rem;
  overflow-y: auto;
}

.metricsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.metricCard {
  background: white;
  border: 1px solid #e2e8f0;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  transition: all 0.2s;
}

.metricCard:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  transform: translateY(-2px);
}

.metricHeader {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.metricTitle {
  font-size: 0.875rem;
  font-weight: 600;
  color: #374151;
  margin: 0;
}

.metricValue {
  font-size: 1.5rem;
  font-weight: bold;
  color: #3b82f6;
}

.metricWarning {
  color: #f59e0b;
}

.metricProgress {
  margin-top: 0.75rem;
}

.progressBar {
  width: 100%;
  height: 0.5rem;
  background: #f1f5f9;
  border-radius: 1rem;
  overflow: hidden;
}

.progressFill {
  height: 100%;
  background: #3b82f6;
  border-radius: 1rem;
  transition: width 0.3s ease;
}

.progressSuccess {
  background: #10b981;
}

.metricDescription {
  font-size: 0.75rem;
  color: #64748b;
  margin: 0.5rem 0 0 0;
}

.metricsComingSoon {
  background: #f8fafc;
  border: 1px solid #e2e8f0;
  border-radius: 0.75rem;
  padding: 3rem 2rem;
  text-align: center;
  color: #64748b;
}

.metricsComingSoon i {
  font-size: 3rem;
  color: #cbd5e1;
  margin-bottom: 1rem;
}

.metricsComingSoon h3 {
  font-size: 1.125rem;
  font-weight: 600;
  color: #374151;
  margin: 0 0 0.5rem 0;
}

.metricsComingSoon p {
  font-size: 0.875rem;
  margin: 0;
  color: #94a3b8;
}

/* Responsive */
@media (max-width: 768px) {
  .metricsGrid {
    grid-template-columns: 1fr;
  }
  
  .metricsHeader {
    padding: 1rem;
  }
  
  .metricsContent {
    padding: 1rem;
  }
}